.navbar {
  font-family: "poppins";
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0 8px 6px -6px gray;
  border-bottom: 0.3px solid gray !important;
}
.nav-link {
  font-weight: 300 !important;
}
.active {
  border-bottom: 2px solid black !important;
}

/* ===============footer================ */
.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-title {
  font-size: 100px;
  font-weight: 700;
}

.pnf-heading {
  font-weight: normal;
}

.pnf-btn {
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}
.pnf-btn:hover {
  background-color: black;
  color: white;
}
.footer {
  color: white !important;
  padding: 25px;
  background-color: black;
  text-decoration: none;
  background: linear-gradient(to right, #434343, #000000);
  
  
}
.footer a {
  text-decoration: none;
  color: white;
  padding: 10px;
}
.footer a:hover {
  border-bottom: 2px solid rgb(244, 230, 230);
  
}
.contactus {
  margin: 0;
  padding: 0;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.register{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 70vh;
}
.product-link{
  text-decoration: none;
  color: black;
  
}
.custom-btn {
  padding: 0.25rem 0.5rem;
  font-size: 0.875rem;
}
.prices-column {
  display: flex;
  flex-direction: column;
}

