/* General Styles */
.banner-img {
    margin-top: 60px;
  }
  
  /* Home Page Styling */
  .home-page {
    display: flex;
    flex-wrap: wrap;
  }
  
  .home-page h1 {
    color: #333;
    font-family: "Playfair Display", serif;
    margin-bottom: 20px;
  }
  
  /* Product Card Styling */
  .home-page .card {
    background-color: #f9f9f9;
    width: 15rem;
    border: none;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .home-page .card:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.2);
  }
  
  .home-page .card-body {
    padding: 1rem;
  }
  
  .home-page .card-title {
    font-weight: bold;
    font-size: 1.1rem;
    margin-bottom: 10px;
  }
  
  .home-page .card-text {
    color: #666;
    font-size: 0.9rem;
    margin-bottom: 15px;
  }
  
  .home-page .card img {
    height: 200px;
    object-fit: cover;
    transition: transform 0.3s ease;
  }
  
  .home-page .card img:hover {
    transform: scale(1.05);
  }
  
  /* Buttons Styling */
  .custom-btn {
    border-radius: 5px;
    font-size: 14px;
    font-weight: bold;
    transition: background-color 0.3s ease, color 0.3s ease;
    padding: 6px 12px;
  }
  
  .custom-btn:hover {
    background-color: #444;
    color: white;
  }
  
  /* Load More Button */
  .btn-warning {
    font-weight: bold;
    font-size: 16px;
    background-color: #ff9800;
    border: none;
    padding: 10px 20px;
    transition: background-color 0.3s ease;
  }
  
  .btn-warning:hover {
    background-color: #e68900;
    color: white;
  }
  
  /* Filter Section Styling */
  .filters h4 {
    color: #333;
    font-family: "Roboto", sans-serif;
    margin-top: 20px;
    margin-bottom: 10px;
    border-bottom: 1px solid #ddd;
    padding-bottom: 10px;
  }
  
  .filters .ant-checkbox-wrapper,
  .filters .ant-radio-wrapper {
    margin: 5px 0;
    font-size: 14px;
  }
  
  .filters button {
    background-color: #d9534f;
    color: white;
    margin-top: 20px;
    width: 100%;
    border: none;
    transition: background-color 0.3s ease;
  }
  
  .filters button:hover {
    background-color: #c9302c;
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .home-page .card {
      width: 100%;
      margin: 10px 0;
    }
  }
  
  @media (max-width: 576px) {
    .home-page h1 {
      font-size: 1.5rem;
    }
  
    .home-page .card img {
      height: 150px;
    }
  
    .home-page .card-title {
      font-size: 1rem;
    }
  }
  